"use client";

import React, { useEffect, useState } from 'react'
import { ACCESS_TOKEN, REFRESH_TOKEN } from '../constant';
import { jwtDecode } from 'jwt-decode';
import api from '../api';
import { Navigate } from 'react-router-dom';

const ProtectedRoute = ({ children }: {children: React.ReactNode}) => {
    const [isAuthorized, setIsAuthorized] = useState(-1);

    useEffect(() => {
      auth().catch(() => setIsAuthorized(0));
    },);

    const refresh = async () => {
      const refreshToken: string | null = localStorage.getItem(REFRESH_TOKEN);

      try{
        const res = await api.post("auth/token/refresh", {refresh: refreshToken});

        if (res.status === 200){
          setIsAuthorized(1);
          localStorage.setItem(ACCESS_TOKEN, res.data.access);
        } else {
          setIsAuthorized(0);
        }        
      } catch (error) {
        console.log("Error in Refreshing Token", error);
        setIsAuthorized(0);
      }
    }

    const auth = async () => {
      const token: string | null = localStorage.getItem(ACCESS_TOKEN);

      if (!token){
        setIsAuthorized(0);
        return;
      }

      let decode;
      try{
        decode = jwtDecode(token);
      }
      catch(error){
        console.log("Error in Decoding", error);
        setIsAuthorized(0)
        return;
      }
      const tokenExpiration: number | undefined = decode.exp;
      const now = Date.now() / 1000;

      if (tokenExpiration && tokenExpiration < now){
        await refresh();
      } else {
        setIsAuthorized(1);
      }
    };

    if (isAuthorized === -1){
      return <div> loading... </div>
    }
  
    return (isAuthorized == 1) ? children : <Navigate to="/auth/login" />;
};

export default ProtectedRoute
